import "./menu.scss"

const Menu = (props) => {
    return (
        <div className={`menu ${props.menuOpen===false?"":"active"}`}>
            <ul>
                <li>
                    <a href="#intro" onClick={props.open}>Home</a>
                </li>
                <li>
                    <a href="#projects" onClick={props.open}>Projects</a>
                </li>
                <li>
                    <a href="#certificates" onClick={props.open}>Certificates</a>
                </li>
                <li>
                    <a href="#education" onClick={props.open}>Education</a>
                </li>
                <li>
                    <a href="#contact" onClick={props.open}>Contact</a>
                </li>
            </ul>
        </div>
    )
}

export default Menu