import data from "./data.js"
import "./projects.scss"
import React from "react"
const Projects = () => {
  return (
    <div className='portfolio container mb-5' id="projects">
      <h1 className=" text-center">Projects</h1>
      <div className="row row-cols-1 row-cols-md-3 g-4">
        {React.Children.toArray(data.map((element) => {
          return <div className="col">
            <div className="card h-100 card text-bg-info">
                <div className="card-header">
                  {element.title}
                </div>
              <div className="card-body">
                <p className="card-text">{element.desc}</p>
                <a href={element.link} target="_blank" rel="noreferrer" className="btn btn-danger" style={{display:element.link===""?"none":'inline'}}>See more</a>
              </div>
            </div>
          </div>
        }))}
      </div>


    </div>
  )
}

export default Projects