import "./topbar.scss"
import EmailSharpIcon from '@mui/icons-material/EmailSharp';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';

const Topbar = (props) => {
  return (
    <div className={`topbar ${props.menuOpen===false?"":"active"}`}>
      <div className="wrapper">
        <div className="left">
          <a href="#intro" className="logo">Mech-IT</a>
          <div className="itemContainer">
            <EmailSharpIcon className="icon"/>
             <span id="mail">pritamdupare440@gmail.com</span>
          </div>
          <div className="itemContainer">
            <GitHubIcon className="icon" id="git"/>
             <a href="https://github.com/Mech-IT" id="git"><span>https://github.com/Mech-IT</span></a>
          </div>
          <div className="itemContainer">
            <LinkedInIcon className="icon" id="linkedinIcon"/>
             <a href="http://www.linkedin.com/in/pritam-dupare-0ab60314a" id="linkedin"><span>http://www.linkedin.com/in/pritam-dupare-0ab60314a</span></a>
          </div>
        </div>
        <div className="right">
          <div className="hamburger" onClick={props.open}>
            <span className="1"></span>
            <span className="2"></span>
            <span className="3"></span>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Topbar