import "./contact.scss"
import React,{ useState,useRef } from "react"
import emailjs from '@emailjs/browser';

const Contact = () => {
  const form = useRef();
 const [message, setMessage] = useState(false)
  const handleSubmit=(e)=>{
    e.preventDefault()
    setMessage(true)
    emailjs.sendForm(`${process.env.REACT_APP_YOUR_SERVICE_ID}`, `${process.env.REACT_APP_YOUR_TEMPLATE_ID}`, form.current, `${process.env.REACT_APP_YOUR_PUBLIC_KEY}`)
      .then((result) => {
          console.log(result.text);
          form.current.reset()
          setTimeout(() => {
            setMessage(false)
          }, 2000);
      }, (error) => {
          console.log(error.text);
      });
  }
  return (
    <div className="contact" id="contact">
      <div className="left">
        <img src="./assets/shake.svg" alt="" />
      </div>
      <div className="right">
        <h2 className="text-center mt-5">Contact</h2>
        <form className="mx-5" onSubmit={handleSubmit} ref={form}>
          <div className="mb-3">
            <label htmlFor="exampleFormControlInput1" className="form-label">Email address</label>
            <input type="email" className="form-control" id="exampleFormControlInput1" placeholder="name@example.com" name="user_email" required/>
          </div>
          <div className="mb-3">
            <label htmlFor="exampleFormControlTextarea1" className="form-label">Message</label>
            <textarea className="form-control" id="exampleFormControlTextarea1" rows="3" name="message" required></textarea>
          </div>
          <div className="mb-3">
          <button type="submit" className="btn btn-primary">Send</button>
          </div>
          {message && <span >Thanks, I will reply ASAP :)</span>}
        </form>
      </div>
    </div>
  )
}

export default Contact