import "./intro.scss"
import me from "./me.png"
import Typewriter from 'typewriter-effect';
const Intro = () => {
  return (
    <div className='intro' id="intro">
      <div className="left">
        <div className="imgContainer">
          <img src= {me} alt="Pritam Dupare" />
        </div>
      </div>
      <div className="right">
        <div className="wrapper">
          <h2>Hello, I am</h2>
          <h1>Pritam Dupare</h1>
          <span >
          <Typewriter 
           options={{ 
           strings:["MERN Stack Developer", "Python Programmer", "Mechanical Engineer"],
           autoStart:true,
           delay:75,
           pauseFor:3000,
           loop:true
           }} 
           /> 
          </span>
          <span className="skills">
         Python, HTML, CSS, JavaScript, React, Bootstrap, Node.js, MongoDB, Microsoft Word, Microsoft PowerPoint 
          </span>
          
        </div>
      </div>
    </div>
  )
}

export default Intro